import { Menu } from '../../template/components.js';
import Head from 'next/head';

export default (props) => {
    let metadata = {"ctime":"2019-10-19T17:55:06.484Z","mtime":"2019-10-19T17:55:06.484Z"};
    
    let pageTitle = (metadata.title ? metadata.title + " | " : "") + "Seb Renauld: The Blog";
    return (<Menu>
        <Head>
            <title key="title">{pageTitle}</title>
            <meta name="description" content={metadata.description || ''} />
            <meta key="og:type" name="og:type" content="article" />
            <meta key="og:title" name="og:title" content={pageTitle} />
            <meta key="og:description" name="og:description" content={metadata.description || ''} />
            {metadata.image && <meta key="og:image" name="og:image" content={metadata.image} />}
            {metadata.url && <meta key="og:url" name="og:url" content={metadata.url} />}
            <meta key="article:published_time" name="article:published_time" content={metadata.ctime} />
            <meta key="article:modified_time" name="article:modified_time" content={metadata.mtime} />
        </Head>
        <div>
            <div class="blog-article" dangerouslySetInnerHTML={{__html: "<h3 id=\"welcometotheblog\">Welcome to the blog!</h3>\n<p>In this section, you'll find periodic articles written about certain technical (and sometimes non-technical) aspects of life. While varied, I hope at least some of them will catch your eye.</p>\n<h4 id=\"latestarticles\">Latest articles</h4>\n<ul>\n<li><a href=\"/blog/openapi-and-apis\"><strong>On OpenAPI, toolings for code generation, and APIs</strong></a>: A quick tour of techniques, tools and pitfalls of programmatic API documentation, along with the first few steps towards a real-world application on a third-party API.</li>\n<li><a href=\"/blog/working-on-the-edge-a-rust-ffi-primer\"><strong>Working on the Edge: FFI and Rust</strong></a>: A deep dive into the Foreign Function Interface and its application in building bridges between Rust and other languages. Also a serie, also episode 1.</li>\n</ul>"}}></div>
        </div>
    </Menu>)
};